.homeContent {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  h1 {
    border-right: solid 3px snow;
    white-space: nowrap;
    overflow: hidden;
    animation: cursor 750ms steps(29, end) infinite;

    &:after {
      content: "";
      width: 100%;
      height: 100%;
      animation:
        text-box 2s steps(29, end) 1s 1 normal both;
      animation: hello-there 2s linear 1;
      animation-fill-mode: forwards;
    }
  }

  h2 {
    border-right: solid 3px snow;
    white-space: nowrap;
    overflow: hidden;
    animation: cursor 750ms steps(29, end) infinite;

    &:after {
      content: "";
      width: 100%;
      height: 100%;
      animation:
        text-box 2s steps(29, end) 1s 1 normal both;
      animation: scroll-down 4s linear 1;
      animation-fill-mode: forwards;
      animation-delay: 2.5s;
    }
  }

}

@keyframes text-box {
  from {
    width: 0;
  }

  to {
    width: 100%;
  }
}

@keyframes cursor {
  from {
    border-right-color: snow;
  }

  to {
    border-right-color: transparent;
  }
}

